var scentWheel = function() {
	var svgWidth;
	var progress = { pct:0 };
	var activeSlide = 0;
	var slideTimeout;
	var delayTime = 10000;
	
	function renderCircle() {
		resizeHandler();

		var r = document.querySelector(".progress").getAttribute('r'); 

		var c = Math.PI*(r*2);
		document.querySelector(".progress").style.strokeDasharray=c;

		var pct = mapRange(progress.pct, 0, 1, 1, 0);
		document.querySelector(".progress").style.strokeDashoffset=pct*c;
	}
	function resizeHandler(e) {
		
			var svgWidth = document.querySelector("svg.progress_container").getBoundingClientRect().width;
			var ratio = 1000/svgWidth;
			
			$(".scent_wheel svg circle.progress")[0].style.strokeWidth=5*ratio;
			$(".scent_wheel svg circle.background")[0].style.strokeWidth=1*ratio;
			$(".scent_wheel svg circle.point").each(function() {
				$(this)[0].setAttribute("r",6*ratio);
			});
			console.log(ratio,"from",svgWidth);
			// setTimeout(resizeHandler,300,e);
		
	}
	function getSlideLength() {
		$currentText = $(".descriptions .section_container section:nth-child("+mapRange(activeSlide+1,0,4,4,0)+") .text");
		var charCount=0;
		$currentText.children().each(function() {
			charCount+=$(this).html().length;
		});
		return mapRange(Math.ceil(charCount*50),3000,15000,8000,17000);
	}
	function setupWheelNavigation() {
		$("g.section .point").bind("click",function(e) {
			e.preventDefault();
			scentWheel.noteSkipTo($(this).attr("data-section"));
			$("g.section circle.point").attr("class","point");
			$(this).attr("class","point active");
		});
		$(".scent_wheel a.discover").bind("click",function(e) {
			e.preventDefault();
			scentWheel.noteNavigate(1,delayTime);
		});
		$(".navigation a.forward").bind("click",function(e) {
			scentWheel.nextNote();
			e.preventDefault();
		});
		$(".navigation a.back").bind("click",function(e) {
			scentWheel.prevNote();
			e.preventDefault();
		});
		$(".navigation ul li").bind("click",function(e) {
			var targetStep=$(this).index()+1;
			if (targetStep==0) {
				scentWheel.noteNavigate(targetStep,0);
			} else {
				scentWheel.noteSkipTo(targetStep);
			}
		});
	}
	return {
		Init: function() {
			setupWheelNavigation();
			window.addEventListener("resize",resizeHandler);
			TweenLite.ticker.addEventListener("tick", renderCircle);
			$(".backgrounds .section_container section:first-child img").addClass("active");
		},
		nextNote: function() {
			targetStep=activeSlide+1;
			if (targetStep==4) {
				targetStep=0;
			}
			scentWheel.noteSkipTo(targetStep);
		},
		prevNote: function() {
			targetStep=activeSlide-1;
			if (targetStep==-1) {
				return false;
			} else {
				scentWheel.noteSkipTo(targetStep);
			}
		},
		noteNavigate: function(step) {
			duration=getSlideLength();
			if (step == 4 || step == 0) {
				step = 0;
				delay=1000;

				$("circle.progress").css("opacity",0);
				
				setTimeout(function() {
					$("circle.progress").css("opacity",1);
				},delay+20);
				duration=0;
			} else {

			}
			if (step>0) {
				$(".scent_wheel").addClass("running");
			} else {
				$(".scent_wheel").removeClass("running");
			}
			var targetPct = step/3;

			var delay=0;
			var targetBackground = step*-100;

			var descriptionStep = mapRange(step,0,3,3,0);
			var targetDescription = descriptionStep*-100;
			var tween = TweenLite.to(progress, duration/1000, { pct: targetPct, delay: delay, ease:Power0.easeOut });

			if (step>0) { // for all slides other than the intro slide auto jump to next step
				slideTimeout = setTimeout(function() {
					scentWheel.noteNavigate(step+1);
				},duration);
			} else {
				clearTimeout(slideTimeout);
			}

			$(".navigation ul li").removeClass("active");
			$(".navigation ul li:nth-child("+step+")").addClass("active");

			$(".backgrounds .section_container img").removeClass("active");
			$(".backgrounds .section_container section:nth-child("+(step+1)+") img").addClass("active");

			$(".descriptions .section_container .text").removeClass("active");
			$(".descriptions .section_container section:nth-child("+(descriptionStep+1)+") .text").addClass("active");

			$("g.section circle.point").attr("class","point");
			$("g.section circle.point:nth-child("+step+")").attr("class","point active");

			$(".backgrounds .section_container").css("transform","translateY("+targetBackground+"%)");
			$(".descriptions .section_container").css("transform","translateY("+targetDescription+"%)");
			activeSlide=step;
		},
		noteSkipTo: function(step) {
			step=parseFloat(step);

			if (step==activeSlide) return false;
			
			var textStep = mapRange(step, 0, 4, 4, 0);
			targetPct=(step-1)/3;
			if (targetPct<0) targetPct = 0;

			targetBackground=step*-100;
			targetDescription=(textStep-1)*-100;
			clearTimeout(slideTimeout);

			var tween = TweenLite.to(progress, 0.3, { pct: targetPct });

			if (step>0) {
				$(".scent_wheel").addClass("running");
			} else {
				$(".scent_wheel").removeClass("running");
			}
			
			$(".navigation ul li").removeClass("active");
			$(".navigation ul li:nth-child("+step+")").addClass("active");

			$(".backgrounds .section_container img").removeClass("active");
			$(".backgrounds .section_container section:nth-child("+(step+1)+") img").addClass("active");

			$(".descriptions .section_container .text").removeClass("active");
			$(".descriptions .section_container section:nth-child("+(textStep)+") .text").addClass("active");

			$("g.section circle.point").attr("class","point");
			$("g.section circle.point:nth-child("+step+")").attr("class","point active");

			$(".backgrounds .section_container").css("transform","translateY("+targetBackground+"%)");
			$(".descriptions .section_container").css("transform","translateY("+targetDescription+"%)");
			
			delayTime=getSlideLength();
			if (step==0) { // if back to intro, don't automatically skip to next slide
				clearTimeout(slideTimeout);
			} else {
				slideTimeout=setTimeout(function() {
					scentWheel.noteNavigate(step,delayTime);
				},1000);
			}
			activeSlide=step;
		}
	}
	
}();
function mapRange(value, low1, high1, low2, high2) {
	return low2 + (high2 - low2) * (value - low1) / (high1 - low1);
}
$(function() {
	if (document.querySelector(".scent_wheel")!=null) scentWheel.Init();
});