var collection = function() {
	var sectionBounds;
	var sectionObj;
	var windowDim = new Object();
	var scrollTop = $(window).scrollTop();
	var snapThreshold = 8;
	var monitorScroll = true;
	var scrollLockPos = 0;
	var resetScrollTimeout;
	var prevScroll = scrollTop;
	var delta;
	var productCount = 0;
	var prevPosition = 0;
	var sectionOffsetTop;

	function scrollMonitor(e) {
		clearTimeout(resetScrollTimeout);
		resetScrollTimeout = setTimeout(resetScroll,1700);
		sectionBounds = sectionObj.getBoundingClientRect();
		scrollTop = $(window).scrollTop();
		delta = scrollTop - prevScroll;
		prevScroll = scrollTop;
		if (sectionBounds.bottom-windowDim.height<0) {
			$(sectionObj).addClass("above");
		} else {
			$(sectionObj).removeClass("above");
		}
		if(sectionBounds.top>0) {
			$(sectionObj).addClass("below");
		} else {
			$(sectionObj).removeClass("below");
		}
		if ( sectionBounds.top <=0 && sectionBounds.bottom >= windowDim.height ) {
			if (monitorScroll && Math.abs(delta)>snapThreshold) {
				scrollSnap(e);
			}
		} else {
			if (sectionBounds.top < windowDim.height/2 && sectionBounds.top > 5 ) { 
				if (delta > 0) {
					setScene(0);
					snapTo($(".productshots .product:first-child", $(sectionObj)).offset().top, 1500, e);
				} else if (delta<0) {
					setScene(-1);
				}
			} else if (sectionBounds.bottom < windowDim.height/2 && sectionBounds.bottom > 5 ) { // snapping when scrolling in from below
				if (delta < 0) {
					setScene(productCount-1);
					snapTo($(".productshots .product:last-child", $(sectionObj)).offset().top, 1500, e);
				} else if (delta>0) {
					setScene(-1);
				}
			} 
		}
	}
	function resetScroll() {
		monitorScroll = true;
	}
	function preventDefault(e) {
	  e = e || window.event;
	  if (e.preventDefault)
	      e.preventDefault();
	  e.returnValue = false;  
	}
	function disableScroll() {
	  if (window.addEventListener) // older FF
	      window.addEventListener('DOMMouseScroll', preventDefault, false);
	  window.onwheel = preventDefault; // modern standard
	  window.onmousewheel = document.onmousewheel = preventDefault; // older browsers, IE
	  window.ontouchmove  = preventDefault; // mobile
	}

	function enableScroll() {
	    if (window.removeEventListener)
	        window.removeEventListener('DOMMouseScroll', preventDefault, false);
	    window.onmousewheel = document.onmousewheel = null; 
	    window.onwheel = null; 
	    window.ontouchmove = null;  
	}
	function scrollSnap(e) {
		var obj = findParentProductContainer(document.elementFromPoint(0,windowDim.height/2));
		var objectActive = $(obj).index();

		if (delta>0) {
			objectTarget = objectActive+1;
			if (objectTarget<productCount) {
				snapTo( $(obj).next().offset().top, 1500, e);	
			} else {
				snapTo( scrollTop + windowDim.height * 3/4, 1500, e); // leaving
				setScene(-1);
			}
		} else {
			objectTarget = objectActive-1;
			if (objectTarget>=0) {
				snapTo( $(obj).prev().offset().top,1500, e);	
			} else {
				snapTo( scrollTop - windowDim.height*3/4, 1500, e); // leaving
				setScene(-1);
			}
		}
		setScene(objectTarget);	
	}
	function setScene(no) {
		$(".cta .product",$(sectionObj)).removeClass("active");
		$(".cta .product:nth-child("+(no+1)+")",$(sectionObj)).addClass("active");
		$("header nav a",$(sectionObj)).removeClass("active");
		$("header nav a:nth-child("+(no+1)+")",$(sectionObj)).addClass("active");
		$(".overlay .product",$(sectionObj)).removeClass("active");
		$(".overlay .product:nth-child("+(no+1)+")",$(sectionObj)).addClass("active");
	}
	function snapTo(position, snapDuration, e) {
		disableScroll();
		monitorScroll = false;
		$('html, body').stop().scrollTo(position, { duration:snapDuration, easing: "easeOutExpo" }, function() {
			monitorScroll = true;
			enableScroll();
		});
	}
	function enableScrollSnap() {
		$("body.collections").addClass("scrollsnap");
	}
	function disableScrollSnap() {
		$("body.collections").removeClass("scrollsnap");
	}
	function findParentProductContainer(obj) {
		if ($(obj).hasClass("product")) {
			return obj;
		} else {
			if (obj==document.body) {
				return false
			} else {
				return findParentProductContainer(obj.parentNode);
			}
		}
	}
	function resizeHandler(e) {
		sectionBounds = sectionObj.getBoundingClientRect();
		windowDim.height = $(window).height();
		windowDim.width = $(window).width();
	}
	function positionFlowers() {
		var scrollProgress = (scrollTop-sectionOffsetTop)/(productCount*windowDim.height);
		var targetPosition = (scrollProgress * $(".overlay",$(sectionObj)).height());
		var targetPos= -1*(targetPosition);
		if (targetPos>windowDim.height*2/5) targetPos = windowDim.height*2/5;
		$(".collection .overlay .fg .left").css("transform","translate3d(0,"+(targetPos*1.1)+"px,50px)");
		$(".collection .overlay .fg .right").css("transform","translate3d(0,"+(targetPos*1.1)+"px,50px)");
		$(".collection .overlay .bg .left").css("transform","translate3d(0,"+(targetPos)+"px,0px)");
		$(".collection .overlay .bg .right").css("transform","translate3d(0,"+(targetPos)+"px,0px)");
		setTimeout(function() {
	        window.requestAnimationFrame(positionFlowers);
			window.requestAnimationFrame(scrollMonitor);
	    }, 16);
	}
	function scrollHandler(e) {
		scrollTop = $(window).scrollTop();
		sectionOffsetTop = $(sectionObj).offset().top;
	}
	function setupSizes(obj) {
		sectionObj = obj;
		productCount = $(".productshots",$(obj)).children().length;
		$(".collection").css("height",(productCount*100)+"vh");
		// $(".overlay").css("height",(productCount*133)+"vh");
		positionFlowers();
	}
	function setupLinks() {
		$(".collection header a").bind("click",function(e) {
			var thisIndex = $(this).index();
			snapTo( $(".productshots .product:nth-child("+(thisIndex+1)+")",$(sectionObj)).offset().top, 1000, e);
			setScene(thisIndex);
			// snapTo
			e.preventDefault();
		})
	}
	return {
		Init: function(obj) {
			setupSizes(obj);
			setupLinks();
			window.addEventListener("resize",resizeHandler);
			window.addEventListener("wheel",scrollMonitor);
			window.addEventListener("scroll",scrollHandler);
			resizeHandler();
			scrollMonitor();
		},
	}
	
}();

$(function() {
	$(".collection").each(function() {
		collection.Init(this);
	})	
});

$.fn.scrollTo = function( target, options, callback ){
  if(typeof options == 'function' && arguments.length == 2){ callback = options; options = target; }
  var settings = $.extend({
    scrollTarget  : target,
    offsetTop     : 50,
    duration      : 500,
    easing        : 'easeOutQuad'
  }, options);
  return this.each(function(){
    var scrollPane = $(this);
    var scrollTarget = (typeof settings.scrollTarget == "number") ? settings.scrollTarget : $(settings.scrollTarget);
    var scrollY = (typeof scrollTarget == "number") ? scrollTarget : scrollTarget.offset().top + scrollPane.scrollTop() - parseInt(settings.offsetTop);
    scrollPane.animate({scrollTop : scrollY }, parseInt(settings.duration), settings.easing, function(){
      if (typeof callback == 'function') { callback.call(this); }
    });
  });
}
jQuery.extend( jQuery.easing, {
	def: 'easeOutQuad',
	easeOutExpo: function (x, t, b, c, d) {
		return (t==d) ? b+c : c * (-Math.pow(2, -10 * t/d) + 1) + b;
	}
});
